.griddle-container {
		border: 1px solid #DDD;
}

.griddle .top-section {
		clear: both;
		display: table;
		width: 100%;
}

.griddle .griddle-filter {
		float: left;
		width: 50%;
		text-align: left;
		color: #222;
		min-height: 1px;
}

.griddle .griddle-settings-toggle {
		float: left;
		width: 50%;
		text-align: right;
		button{
			height: 30px;
			padding: 0 15px;
			background: #eee;
			color: $color6;
			line-height: 26px;
			font-weight: 600;
			border-radius: 15px;
			border: 1px solid #ddd;
			font-size: 13px;
		}
}

.griddle .griddle-settings {
		background-color: #FFF;
		border: 1px solid #DDD;
		color: #222;
		padding: 10px;
		margin-bottom: 10px;
}

.griddle .griddle-settings .griddle-columns {
		clear: both;
		display: table;
		width: 100%;
		border-bottom: 1px solid #EDEDED;
		margin-bottom: 10px;
}

.griddle .griddle-settings .griddle-column-selection {
		float: left;
		width: 20%;

}

.griddle table {
		width: 100%;
		table-layout: fixed;
}

.griddle th {
		background-color: $color6;
		border-bottom: 1px solid #DDD;
		padding: 5px;
		background-color: #c30;
		color: #fff;
		line-height: 25px;
		height: 50px;
		cursor: pointer;
		border-bottom: 10px solid #ccc;
		span.fa{
			padding-left: 10px;
		}
}

.griddle td {
		padding: 5px;
		background-color: #FFF;
		border-top-color: #DDD;
		color: #222;
		img{
			width: 100%;
			height: auto;
		}
}

.griddle .footer-container {
		padding: 0px;
		background-color: #EDEDED;
		border: 0px;
		color: #222;
}

.griddle .griddle-previous, .griddle .griddle-page, .griddle .griddle-next {
		float: left;
		width: 33%;
		min-height: 1px;
		vertical-align: middle;
		button{
			height: 30px;
			margin-left: 20px;
			padding: 0 25px;
			background: white;
			color: $color6;
			line-height: 26px;
			font-weight: 600;
			border-radius: 15px;
			border: 1px solid #ddd;
			font-size: 13px;
		}
}

.griddle .griddle-page {
		text-align: center;
}

.griddle .griddle-next {
		text-align: right;
}

.griddle {
	margin-top: 10px;
	.top-section{
		margin-bottom: 5px;
		.griddle-filter{
			.filter-container{
				input{
					width: 200px;
					margin-left: 10px;
					height: 30px;
					border: none;
					padding-left: 10px;
					-moz-border-radius: 15px;
